import logo from './logo.svg';
import './App.css';
import { ClientJS } from 'clientjs/src/client.base';
import fingerprintjs from '@fingerprintjs/fingerprintjs';
import { useEffect, useState } from 'react';

function App() {

  const [fp2, setFp2] = useState("");

  const fingerprintClientJs = () => {
    var client = new ClientJS;
    return client.getFingerprint();
  }

  const fingerprintJs = () => {
    console.log(fingerprintjs.load().then(fp => fp.get()).then(result => {
      setFp2(result.visitorId);
    }));
    // const fpPromise = fingerprintjs
    //   .then(FingerprintJS => FingerprintJS.load())
    //   .then(fp => fp.get())
    //   .then(result => {
    //     // This is the visitor identifier:
    //     const visitorId = result.visitorId
    //     setFp2(visitorId);
    //   })
  }

  useEffect(() => {
    fingerprintJs();
  }, [])

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          ClientJs Fingerprint: {fingerprintClientJs()}
          <br />
          FingerprintJs: {fp2}
        </p>
      </header>
    </div>
  );
}

export default App;
